/*
* Colors
*/
:root {
    --base-color: #001f3f; /* Navy blue */
    --text-color: #ffffff; /* White text */
}

/*
* Media queries helper
*/
@media (max-width: 768px) {
    .login__welcome-back__logo-container {
        font-size: 0;
        justify-content: center;
    }
}

/*
* Classes (ampersand + BEM)
*/
.login {
    display: flex;
    height: 100%;
}

.login__welcome-back {
    z-index: 2;
    transition: visibility .3s ease-in-out, transform .8s ease-in-out; /* Combined transition */
    position: absolute;
    width: 30%;
    font-family: 'Quicksand-Bold';
}

.login__welcome-back--active {
    visibility: visible;
    transform: translateX(0%);
}

.login__welcome-back--inactive {
    visibility: hidden;
    transform: translateX(-100%);
}

.login__welcome-back__logo-container {
    padding: 15px;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: var(--text-color); /* White text */
    font-weight: bold;
}

.login__welcome-back__logo-container--image {
    width: 38px;
    height: 38px;
    margin-right: 4px;
}

.login__welcome-back__main-container {
    margin-top: 20%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--text-color); /* White text */
}

.login__welcome-back__main-container__text-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 60%;
    text-align: center;
}

.login__welcome-back__main-container__text-container--title {
    font-size: 30px;
    font-weight: bold;
}

.login__welcome-back__main-container__text-container--secondary {
    margin-top: 12px;
    font-size: 13px;
    font-weight: lighter;
    font-family: 'Quicksand-Regular';
}

.login__welcome-back__main-container__button-container {
    margin-top: 40px;
    border: solid 2px var(--text-color); /* White border */
    width: 50%;
    height: 40px;
    border-radius: 30px;
    font-size: 18px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background-color: var(--base-color); /* Navy blue background */
}

.login__create-container {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: var(--base-color); /* Navy blue text */
    font-weight: bold;
    font-size: 30px;
    z-index: 2;
    width: 70%;
    left: 30.5%;
    transition: all .8s ease-in-out;
    font-family: 'Quicksand-Bold';
}

.login__create-container--active {
    visibility: visible;
    transform: translateX(0%);
}

.login__create-container--inactive {
    visibility: hidden;
    transform: translateX(100%);
}

.login__create-container__social-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-top: 5px;
}

.login__create-container__social-container--facebook-icon {
    width: 30px;
    height: 30px;
    cursor: pointer;
}

.login__create-container__social-container--google-icon {
    width: 22px;
    height: 22px;
    margin-left: 10px;
    cursor: pointer;
}

.login__create-container__social-container--linkedin-icon {
    width: 22px;
    height: 22px;
    margin-left: 14px;
    cursor: pointer;
}

.login__create-container--info-text {
    margin-top: 15px;
    color: #AEAEAE;
    font-size: 14px;
}

.login__create-container__form-container {
    margin-top: 3%;
}

.login__create-container__form-container__form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login__create-container__form-container__form input {
    width: 290px;
    height: 34px;
    border-radius: 3px;
    background-color: #f1f1f1;
    margin-bottom: 10px;
    color: #242424;
}

.login__create-container__form-container__form--name {
    background-image: url(../assets/images/user.svg);
    background-repeat: no-repeat;
    background-position: 10px center;
    padding-left: 40px;
    font-size: 15px;
    background-size: 23px;
}

.login__create-container__form-container__form--email {
    background-image: url(../assets/images/mail.svg);
    background-repeat: no-repeat;
    background-position: 10px center;
    padding-left: 40px;
    font-size: 15px;
    background-size: 23px;
}

.login__create-container__form-container__form--password {
    background-image: url(../assets/images/password.svg);
    background-repeat: no-repeat;
    background-position: 10px center;
    padding-left: 40px;
    font-size: 15px;
    background-size: 23px;
}

.login__create-container__form-container__form--submit {
    margin-top: 20px;
    color: white;
    background-color: var(--base-color);
    border: solid 2px var(--text-color); /* White border */
    width: 50%;
    height: 40px;
    border-radius: 30px;
    font-size: 18px;
    cursor: pointer;
}

.login__login-container {
