.login__hello-container {
    width: 30%;
    left: 70.5%;
    position: absolute;
    z-index: 2;
    transition: all .8s ease-in-out;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10%;
    color: var(--text-color); /* White text */
